import React from 'react';
import styles from './ProfileInfo.module.css';
import backgroundImage from '../../../assets/images/bg1.jpg'
import ProfileStatus from "./ProfileStatus";
import Loader from "../../commot/Loader/Loader";
import ProfileStatusWithHook from "./ProfileStatusWithHook";

const ProfileInfo = (props) => {
    if (!props.profilePage.profile ) { return <Loader/>}

    return (
      <>
        <div>
          <img className={styles.bg} src={backgroundImage}/>
        </div>
          {/*<ProfileStatus status={props.profilePage.status} setStatusThunkCreator={props.setStatusThunkCreator}/>*/}
          <ProfileStatusWithHook status={props.profilePage.status} setStatusThunkCreator={props.setStatusThunkCreator}/>
        <div className={styles.profileInfo}>
          <div>1</div>
    <div>Name: {props.profilePage.profile.fullName}</div>
    <div>About: {props.profilePage.profile.aboutMe}</div>
    <div>ID: {props.profilePage.profile.userId}</div>
    <div>2</div>
        </div>
      </>
  )
}

export default ProfileInfo;