import React, { useState } from 'react';
import profileImage from '../../assets/images/cup.png';
import styles from './Users.module.css';
import {NavLink} from 'react-router-dom';
import * as axios from "axios";
import {usersAPI} from "../../api/api";
import {follow, setFollowingInProgress, unfollow} from "../../redux/users-reducer";
import { useEffect } from 'react';

let User = (props) => {

    return <div>
            <NavLink to={'/profile/' + props.id}>
                <img src={props.avatar != null ? props.avatar : profileImage} alt=""/>
            </NavLink>
            <div>{props.name}</div>
            <div>{props.status != null ? props.status : 'no status'}</div>
            <div>{'i.location.country'}</div>
            <div>{'i.location.city'}</div>
            {props.followed ?
                <button disabled={props.state.followingInProgress.some(id => id === props.id)} onClick={() => {
                    props.unfollowingThunkCreator(props.id)
                }}>Unfollow</button>
                :
                <button disabled={props.state.followingInProgress.some(id => id === props.id)}  onClick={() => {
                    props.followingThunkCreator(props.id)
                }}>Follow</button>

            }
        </div>
}

export default User;

