import {usersAPI} from "../api/api";
import * as axios from "axios";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const CHANGE_PAGE = 'CHANGE_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const SET_IS_FETCHED = 'SET_IS_FETCHED';
const SET_FOLLOWING_IN_PROGRESS = 'SET_FOLLOWING_IN_PROGRESS';

let initialState = {
    users: [],
    totalUsersCount: 0,
    pageSize: 5,
    currentPage: 1,
    isFetched: true,
    followingInProgress: [],
    portionSize: 5
};


const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: state.users.map(i => {
                    if (i.id === action.userId) {
                        return {...i, followed: true}
                    } else {
                        return i
                    }
                })
            }
        case UNFOLLOW:
            return {
                ...state,
                users: state.users.map(i => {
                    if (i.id === action.userId) {
                        return {...i, followed: false}
                    } else {
                        return i
                    }
                })
            }
        case SET_USERS:
            return {...state, users: action.users}
        case CHANGE_PAGE:
            return {...state, currentPage: action.page}
        case SET_TOTAL_USERS_COUNT:
            return {...state, totalUsersCount: action.total}
        case SET_IS_FETCHED:
            return {...state, isFetched: action.isFetched}
        case SET_FOLLOWING_IN_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFollowing
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id != action.userId)
            }
        default:
            return state;
    }
}

export const getUsersThunkCreator = (currentPage, pageSize) => (dispatch) => {
    dispatch(setIsFetched(true));
    usersAPI.getUsers(currentPage, pageSize).then(data => {
        dispatch(setIsFetched(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
    })
}

export const unfollowingThunkCreator = (userId) => (dispatch) => {
    dispatch(setFollowingInProgress(true, userId));
    usersAPI.unfollowing(userId)
        .then(response => {
            if (response.data.resultCode === 0) {
                {
                    dispatch(unfollow(userId))
                }
            }
            dispatch(setFollowingInProgress(false, userId));
        })
}

export const followingThunkCreator = (userId) => (dispatch) => {
    dispatch(setFollowingInProgress(true, userId));
    usersAPI.following(userId)
        .then(response => {
            if (response.data.resultCode === 0) {
                {
                    dispatch(follow(userId))
                }
            }
            dispatch(setFollowingInProgress(false, userId));
        })

}



export const follow = (userId) => ({type: FOLLOW, userId: userId});
export const unfollow = (userId) => ({type: UNFOLLOW, userId: userId});
export const setUsers = (users) => ({type: SET_USERS, users: users});
export const changePage = (page) => ({type: CHANGE_PAGE, page: page});
export const setTotalUsersCount = (total) => ({type: SET_TOTAL_USERS_COUNT, total: (Math.ceil(total / 30))});
export const setIsFetched = (isFetched) => ({type: SET_IS_FETCHED, isFetched});
export const setFollowingInProgress = (isFollowing, userId) => ({type: SET_FOLLOWING_IN_PROGRESS, isFollowing, userId});


export default usersReducer;


