import React from 'react';
import { connect } from 'react-redux';
import Users from './Users';
import {
    follow,
    unfollow,
    setUsers,
    changePage,
    setTotalUsersCount,
    setIsFetched,
    setFollowingInProgress,
    getUsersThunkCreator,
    followingThunkCreator,
    unfollowingThunkCreator
} from './../../redux/users-reducer';
import Loader from '../commot/Loader/Loader';



class UsersContainerComponent extends React.Component {
    componentDidMount() {
        this.props.getUsersThunkCreator(this.props.state.currentPage,this.props.state.pageSize )
    }

    onChangePage = (page) => {
        this.props.changePage(page);
        this.props.getUsersThunkCreator(page,this.props.state.pageSize )
    }

    render() {return <>
        {this.props.state.isFetched ? <Loader/> : null}
        <Users state = {this.props.state} onChangePage = {this.onChangePage} follow={this.props.follow} unfollow={this.props.unfollow} setFollowingInProgress={this.props.setFollowingInProgress}
               followingThunkCreator = {this.props.followingThunkCreator} unfollowingThunkCreator = {this.props.unfollowingThunkCreator}/>
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        state: state.usersPage
    }
}

