const UsersContainer = connect(mapStateToProps, {
    follow,   
    unfollow,
    setUsers,
    changePage,
    setTotalUsersCount,
    setIsFetched,
    setFollowingInProgress, getUsersThunkCreator, followingThunkCreator, unfollowingThunkCreator})(UsersContainerComponent);

export default UsersContainer;