import React from "react";


class ProfileStatus extends React.Component {
    state = {
        editMode: false,
        status: this.props.status
    }

    editModeOn = () => {
        this.setState({
          editMode: true
        })
    }

    editModeOff = () => {
        this.setState({
            editMode: false
        });
        this.props.setStatusThunkCreator(this.state.status);
    }

    onChangeStatus = (e) => {
        this.setState({
            status: e.currentTarget.value
        })
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.status !== this.props.status) {
            this.setState( {status: this.props.status} )
        }
    }


    render()
    {
        return (
            <div>
                <h3>My status:</h3>
                {!this.state.editMode &&
                <div>
                    <span onDoubleClick={this.editModeOn}>{this.props.status ? this.props.status : '--------'}</span>
                </div>
                }
                {this.state.editMode &&
                <div>
                    <input onChange={this.onChangeStatus} autoFocus={true} onBlur={this.editModeOff} value={this.state.status}></input>
                </div>
                }
            </div>
        )
    }
}

export default ProfileStatus;