import React from 'react';
import styles from './Header.module.css';
import { NavLink } from 'react-router-dom';

const Header = (props) => {
    return <header className={styles.header}> <img src="logo192.png"/>
    <br/>
{ props.isLogin ? <div><div>{props.login}</div><button onClick={props.logoutThunkCreator} >Logout</button></div> :  <NavLink to={'/login'}><div>Login</div></NavLink> }

    </header>
}

export default Header;