import React, { useState } from 'react';
import profileImage from '../../assets/images/cup.png';
import styles from './Users.module.css';
import {NavLink} from 'react-router-dom';
import * as axios from "axios";
import {usersAPI} from "../../api/api";
import {follow, setFollowingInProgress, unfollow} from "../../redux/users-reducer";
import { useEffect } from 'react';

let Paginator = (props) => {

    let [currentPortion, setCurrentPortion] = useState(1);
    let firstPage = ((currentPortion - 1) * props.state.pageSize)+1; 
    let lastPage = currentPortion*props.state.pageSize;


    let pagesCount = Math.ceil(props.state.totalUsersCount / props.state.pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let prevFunc = () => {
        setCurrentPortion(currentPortion-1);
        props.onChangePage(firstPage-5)
    }

    let nextFunc = () => {
        setCurrentPortion(currentPortion+1);
        props.onChangePage(lastPage+1)
    }

    let totalPortions = pagesCount / props.state.portionSize;

    return <div>
        
        {currentPortion > 1 ? <button onClick={prevFunc}>Prev</button> : ''}

        {pages.filter(p => p >= firstPage && p <= lastPage ).map(p => {
                return <span className={props.state.currentPage === p ? styles.active : ''}
                             onClick={() => props.onChangePage(p)}>{p}</span>
            })}

        {currentPortion + 1 <= totalPortions ? <button onClick={nextFunc}>Next</button> : ''}

    </div>
    
    // -----------------------------------------------------------------------
    // <div>
    //         {pages.map(p => {
    //             return <span className={props.state.currentPage === p ? styles.active : ''}
    //                          onClick={() => props.onChangePage(p)}>{p}</span>
    //         })}
    //     </div>
}

export default Paginator;

