import * as axios from "axios";

let instanceOfAxios = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "aeec4fd3-bc20-49ab-ba45-91afe579d475"
    }

})

export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 5) {
        return instanceOfAxios.get(`users?page=${currentPage}&count=${pageSize}`).then(response => response.data)
    },
    unfollowing(userId) {
        return instanceOfAxios.delete(`follow/${userId}`)
    },
    following(userId) {
        return instanceOfAxios.post(`follow/${userId}`)
    },
    getProfile(userId) {
        console.warn('Use profileAPI');
        return profileAPI.getProfile(userId);
    }
}

export const profileAPI = {
    getProfile(userId) {
        return instanceOfAxios.get(`profile/${userId}`)
    },
    getStatus(userId) {
        return instanceOfAxios.get(`profile/status/${userId}`)
    },
    setStatus(status) {
        return instanceOfAxios.put(`profile/status`, {status: status})
    }
}

export const authAPI = {
    me() {
        return instanceOfAxios.get(`auth/me`)
    },
    login(email, password, rememberMe = false) {
        return instanceOfAxios.post('auth/login', {email, password, rememberMe})
    },
    logout() {
        return instanceOfAxios.delete('auth/login')
    }
}

