import {profileAPI, usersAPI} from "../api/api";

const ADD_NEW_POST = 'ADD-NEW-POST';
const SET_PROFILE = 'SET_PROFILE';
const SET_STATUS = 'SET_STATUS';

let initialState = {
    posts:[
        {id: 1, post: 'Hello world!', likes: 10},
        {id: 2, post: 'Hello!', likes: 5},
        {id: 3, post: 'Another post!', likes: 2}
      ],
    profile: null,
    status: ''
};


const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_NEW_POST: { 
            let newPost = {
                    id: 5,
                    post: action.newPostText,
                    likes: 0
            };
            let stateCopy = {...state};
            stateCopy.posts = [...state.posts];
            stateCopy.posts.push(newPost);
            stateCopy.textValue = '';
            return stateCopy;
        }
        case SET_PROFILE: {
            return {...state, profile: action.profile}
        }
        case SET_STATUS: {
            return {...state, status: action.status}
        }
        default:
            return state;
    }
}

export const addNewPostActionCreator = (newPostText) => ({ type: ADD_NEW_POST, newPostText });
export const setProfile = (profile) => ({type: SET_PROFILE, profile});
export const setStatus = (status) => ({ type: SET_STATUS, status})

export const getProfileThunkCreator = (userId) => (dispatch) => {
    usersAPI.getProfile((userId))
        .then(response => {
            dispatch(setProfile(response.data));
        })
}

export const getStatusThunkCreator = (userId) => (dispatch) => {
    profileAPI.getStatus(userId)
        .then(response => {
            dispatch(setStatus(response.data))
        })
}

export const setStatusThunkCreator = (status) => (dispatch) => {
    profileAPI.setStatus(status)
        .then (response => {
            if (response.data.resultCode === 0) {
                dispatch(setStatus(status))
            }
        })
}

export default profileReducer;


