const MyPosts = React.memo((props) => {

    let posts = props.posts.map(p => <Post message={p.post} key={p.id} likes={p.likes}/>)


    let onSubmit = (formData) => {
        props.createNewPost(formData.newPostText)
    }

    return (<div className={styles.myPosts}>
            <div>
                <h3>My posts</h3>
                <div>
                    <CreateNewPostFormRedux onSubmit={onSubmit}/>
                </div>
            </div>
            <div className={styles.posts}>
                {posts}
            </div>
        </div>
    )
})

const maxLength10 = maxLengthValidator(10);

const CreateNewPostForm = (props) => {
  return (
      <form onSubmit={props.handleSubmit}>
        <Field name='newPostText' placeholder='Enter your post' component={Textarea} validate={[requiredValidator, maxLength10]}/>
        <button>New post</button>
      </form>
  )
}

const CreateNewPostFormRedux = reduxForm({ form: 'createNewPost' })(CreateNewPostForm)

export default MyPosts;