const ADD_NEW_DIALOG = 'ADD-NEW-DIALOG';

let initialState = {
    dialogs:[
        {id:1, name:'Max'},
        {id:2, name:'John'},
        {id:3, name:'Alex'},
        {id:4, name:'Bob'},
      ],
    messages:[
        {id:1, message:'Hi'},
        {id:2, message:'Hello'},
        {id:3, message:'Yo'},   
        {id:4, message: 'ok'}
    ]
};


const dialogReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_NEW_DIALOG: 
            let body = action.message;
            return {
                ...state,
                messages: [...state.messages, {id:5, message: body}],
                dialogValue : ''
            }
        default:
            return state;
    }
}

export const addNewDialogActionCreator = (message) => ({ type: ADD_NEW_DIALOG, message });


export default dialogReducer;