import React from 'react';
import { Field, reduxForm } from 'redux-form';
import {Input} from "../commot/FormElements/FormElements";
import {requiredValidator} from "../commot/Validators/Validators";
import {connect} from "react-redux";
import {loginThunkCreator} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";

const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div><Field name={'login'} placeholder='Login' component={Input} validate={[requiredValidator]} /></div>
            <div><Field name='password' placeholder='Password' component={Input} validate={[requiredValidator]} type='password'/></div>
            <div><Field name='rememberMe' type='checkbox' component={Input} /> remember me</div>
            <button>Login</button>
        </form>
    )
}

const LoginFormRedux = reduxForm({ form: 'loginform '})(LoginForm);

const Login = (props) => {

    let onSubmit = (formData) => {
        props.loginThunkCreator(formData.login, formData.password, formData.rememberMe)
    }

    if (props.isLogin) { return <Redirect to='/profile' />
    }

    return (
        <>
        <h1>LOGIN</h1>
        <LoginFormRedux onSubmit={onSubmit}/>
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        isLogin : state.auth.isLogin
    }
}

export  default  connect(mapStateToProps,{loginThunkCreator})(Login);