import {authAPI} from "../api/api";

const SET_USER_STATUS = 'SET_USER_STATUS';

let initialState = {
    id: null,
    login: null, 
    email: null,
    isLogin: false
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_STATUS:
            return {
                ...state, ...action.data
            }
        default:
            return state;
    }
}

export const setUserStatus = (id, login, email, isLogin) => ({ type: SET_USER_STATUS, data: {id, login, email, isLogin}})

export const setMeThunkCreator = () => (dispatch) => {
    return authAPI.me()
        .then(response => {
            if (response.data.resultCode === 0) {
                let {id, login, email} = response.data.data;
                dispatch(setUserStatus(id, login, email,  true))
            }
        })
}

export const loginThunkCreator = (email, password, rememberMe = false) => (dispatch) => {
    authAPI.login(email, password, rememberMe)
        .then(response => {
            if (response.data.resultCode === 0 ) {
                dispatch(setMeThunkCreator())
            }
        })
}

export const logoutThunkCreator = () => (dispatch) => {
    authAPI.logout()
        .then (response => {
            if (response.data.resultCode === 0 ) {
                dispatch(setUserStatus(null, null, null, false))
            }
        })
}

export default authReducer;


