import React, { Suspense } from 'react';
import './App.css';
import HeaderContainer from './components/Header/HeaderContainer';
import Nav from './components/Nav/Nav';
import Profile from './components/Profile/Profile';
// import DialogsContainer from './components/Dialogs/DialogsContainer';
import News from './components/News/News';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import {BrowserRouter, Route, withRouter} from "react-router-dom";
import UsersContainer from './components/Users/UsersContainer';
// import ProfileContainer from './components/Profile/ProfileContainer';
import Login from './components/Login/Login';
import {compose} from "redux";
import {initializedThunkCreator} from "./redux/app-reducer";
import {connect} from "react-redux";
import Loader from "./components/commot/Loader/Loader";

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));


class App extends React.Component {
    componentDidMount() {
        this.props.initializedThunkCreator()
    };


    render() {
        if (!this.props.initialized) {return <Loader/> }

        return (
            // <BrowserRouter>
                <div className='app-wrapper'>
                    <HeaderContainer/>
                    <Nav state={this.props.store.getState().sidebar}/>
                    <div className='app-wrapper-content'>
                        {/* <Route path='/profile' render={ () => <ProfileContainer store={props.store}/>}/> */}
                        <Route path='/profile/:userId?' render={() =>   <Suspense fallback={<div>Загрузка...</div>}><ProfileContainer/></Suspense>}/>
                        <Route path='/messages' render={() => <Suspense fallback={<div>Загрузка...</div>}><DialogsContainer store={this.props.store}/></Suspense>}/>
                        <Route path='/users' render={() => <UsersContainer/>}/>
                        <Route path='/news' render={() => <News/>}/>
                        <Route path='/music' render={() => <Music/>}/>
                        <Route path='/settings' render={() => <Settings/>}/>
                        <Route path='/login' render={() => <Login/>}/>


                        {/* <Nav state={props.state.sidebar}/> */}
                        {/* <Route path='/profile' render={ () => <Profile state={props.state.profilePage} dispatch={props.dispatch}/>}/> */}
                        {/* <Route path='/messages' render={ () => <Dialogs state={props.state.dialogPage} dispatch={props.dispatch}/>}/> */}
                    </div>
                </div>
            // </BrowserRouter>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        initialized: state.app.initialized
    }
}

export default compose( connect(mapStateToProps, {initializedThunkCreator}))(App);
